.createBuildGrid{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
}

.button{
    background-color: orange;
}

.button:hover{
    background-color: peru;
}

.signature{
    flex-grow: 2;
    flex-basis: 75%;
}

.grenade {
  flex-grow: 1;
  flex-basis: 15%;
}

.weapon {
  flex-grow: 2;
  flex-basis: 35%;
}

.sidearm {
  flex-grow: 1;
  flex-basis: 15%;
}

.armor {
  flex-grow: 2;
  flex-basis: 45%;
}

.skill {
  flex-grow: 2;
  flex-basis: 45%;
}

.icon {
  float: left;
  margin: 5px;
  height: 100px;
}

.weapon .icon {
  height: 75px;
}

.name {
  float: left;
  font-size: medium;
}

.named {
  color: orange;
}

.unselected {
  margin: auto;
}
